export const sourceCode = "# 消耗品の発注フロー\r\n\r\n毎週 **火曜日・木曜日** に、エフピコ等の消耗品発注を行います。\r\n\r\n## 発注日の自動計算\r\n\r\n発注書の日付計算には、以下の関数を使用しています。\r\n曜日によって納品日が異なるため、手入力ではなく必ずこの数式を使用してください。\r\n\r\n### J4 セル用 数式\r\n\r\n以下の数式をコピーして、`J4` セルに貼り付けてください。\r\n\r\n```excel filename=\"納品日計算ロジック(J4)\"\r\n=LET(\r\n  Today, TODAY(),\r\n  Weekday, WEEKDAY(Today),\r\n  DeliveryDate, IF(Weekday=3, Today+2, Today+3),\r\n  DeliveryDate\r\n)\r\n```\r\n\r\n## 作業フロー\r\n\r\n1.  **前回ファイルの複製**\r\n    - `エフピコ発注書.xlsx` の最新版をコピーし、ファイル名を当日の日付に変更します。\r\n2.  **日付の更新**\r\n    - 上記の数式を `J4` に貼り付け、納品日が正しいか確認します。\r\n3.  **発注数の入力**\r\n    - 在庫を確認し、必要数を入力します。\r\n4.  **メール送信**\r\n    - 作成したファイルを添付し、本社担当者（江藤・日下部）へ送信します。";
